.product-card {
	position: relative;
	z-index: 2;
	overflow: hidden;
	border: 1px solid $colorBorderGrey;
	border-radius: 12px;
	background-color: $colorWhite;

	@media (any-hover: hover) {
		&:hover {
			.product-card__favorite {
				opacity: 1;
			}

			.swiper-button-prev,
			.swiper-button-next {
				opacity: 1;
			}
		}
	}

	@media (any-hover: none) {
		.product-card__favorite {
			opacity: 1;
		}

		.swiper-button-prev,
		.swiper-button-next {
			opacity: 1;
		}
	}

	// .product-card__label

	&__label {
		padding: 6px 15px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 20px;
		left: 0;
		z-index: 5;
		border-radius: 0px 6px 6px 0px;
		background-color: $colorRed;

		@include adaptiv-value("font-size", 14, 12, 1);
		font-weight: 400;
		line-height: 18 / 14 * 100%;
		color: $colorWhite;
	}

	// .product-card__favorite

	&__favorite {
		width: 35px;
		height: 35px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 20px;
		right: 20px;
		z-index: 5;
		border-radius: 6px;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
		opacity: 0;
		transition: fill .2s ease .0s;
		background-color: $colorOrangeHover;

		stroke: $colorRed;

		&._active {
			fill: $colorRed;
		}
	}

	// .product-card__slider

	&__slider {
		position: relative;
		overflow: hidden;

		&:not(:last-child) {
			margin-bottom: 10px;
		}

		.swiper-wrapper {
			display: inline-flex;
		}

		.swiper-button-prev,
		.swiper-button-next {
			width: 30px;
			height: 30px;
			display: inline-flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 50%;
			cursor: pointer;
			opacity: 0;
			border-radius: 50%;
			transform: translateY(-50%);
			border: 1px solid $colorBorderGrey;
			background-color: $colorWhite;

			&:before {
				content: "";
				width: 8px;
				height: 14px;
				background-image: url("../../../public/img/icon/arrow.svg");
				background-size: contain;
				background-position: center;
				background-repeat: no-repeat;
			}
		}

		.swiper-button-prev {
			left: 10px;
		}

		.swiper-button-next {
			right: 10px;

			&:before {
				transform: rotate(180deg);
			}
		}
	}

	// .product-card__slide

	&__slide {}

	// .product-card__slide-link

	&__slide-link {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;

		@extend %unselectable;

		img {
			max-width: 100%;
		}
	}

	// .product-card__content

	&__content {
		padding: 0px 15px 25px;
	}

	// .product-card__title

	&__title {
		-webkit-line-clamp: 1;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		overflow: hidden;

		text-decoration: none;
		text-align: center;
		font-size: 18px;
		font-weight: 600;
		line-height: 22 / 18 * 100%;
		color: $colorDark;

		&:not(:last-child) {
			margin-bottom: 10px;
		}

		@media (any-hover: hover) {
			transition: color .2s ease .0s;

			&:hover {
				color: $colorOrange;
			}
		}
	}

	// .product-card__text

	&__text {
		-webkit-line-clamp: 2;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		overflow: hidden;

		font-size: 14px;
		font-weight: 400;
		line-height: 17 / 14 * 100%;
		color: $colorGrey;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	// .product-card__footer

	&__footer {
		display: flex;
		align-items: flex-start;
	}
}

.product-card-footer {

	// .product-card-footer__row

	&__row {
		display: flex;
		flex-direction: column;
		flex: 1 1 auto;
	}

	// .product-card-footer__text

	&__text {
		font-size: 12px;
		font-weight: 400;
		line-height: 12 / 10 * 100%;
		color: #B4B4B4;
	}

	// .product-card-footer__price

	&__price {
		@include adaptiv-value("font-size", 16, 14, 1);
		font-weight: 600;
		line-height: 19 / 16 * 100%;
		color: $colorDark;

		// .product-card-footer__price_stock

		&_stock {
			text-decoration: line-through;
			color: $colorGrey;
		}
	}

	// .product-card-footer__basket

	&__basket {
		padding-left: 10px;
		padding-right: 10px;
	}

	// .product-card-footer__basket-text

	&__basket-text {
		&:not(:last-child) {
			margin-right: 8px;
		}
	}

	// .product-card-footer__basket-icon

	&__basket-icon {
		display: inline-flex;
	}
}