//< " ФАЙЛ С ОБНУЛЯЮЩИМИ СТИЛЯМИ " >=============================================================================================================>//
@import "./config/_nullstyle";

//< " ПОДКЛЮЧАЕМ ФАЙЛ С ШРИФТАМИ " >=============================================================================================================>//
@import "./config/_fonts";

//< " МИКСИНЫ " >=============================================================================================================>//
@import "./config/_mixins";

//< " ФАЙЛ С ПЕРЕМЕННЫМИ " >=============================================================================================================>//
@import "./config/_vars";

//< " ФАЙЛ С ОТДЕЛЬНЫМИ БЛОКАМИ " >=============================================================================================================>//
@import "./config/_ui";

//< " ФАЙЛ С ОТДЕЛЬНЫМИ БЛОКАМИ " >=============================================================================================================>//
@import "./chunks/_buttons";

@import "./chunks/_product-card";

//< " ТЕЛО САЙТА " >=============================================================================================================>//
body {
   background-color: $colorBg;

   font-family: $fontFamily;
   color: $colorDark;
   font-size: 16px;

   // ._lock-scroll
   &._lock-scroll {
      overflow: hidden;
   }
}

//< " ОБОЛОЧКА/СЕТКА " >=============================================================================================================>//

.wrapper {
   min-height: 100%;
   display: flex;
   flex-direction: column;
   overflow: hidden;
}

._container {
   padding: 0px $containerPadding/2+px 0px $containerPadding/2+px;
   margin: 0px auto;
   max-width: $maxWidthContainer + px;
}

#root {
   height: 100%;
}

.page {
   @include adaptiv-value("padding-top", 50, 30, 1);
   @include adaptiv-value("padding-bottom", 150, 100, 1);
}

//< " ФАЙЛЫ СТИЛЕЙ ХЕАДЕР/МЕНЮ/ФУТЕР И Т.Д. " >=============================================================================================================>//
@import "./modules/_menu";
@import "./modules/_search";
@import "./modules/_category";

@import "./modules/_header";
@import "./modules/_footer";

//< " ФАЙЛЫ СТИЛЕЙ СТРАНИЦ/СЕКЦИЙ " >=============================================================================================================>//
@import "./sections/_not-found";

@import "./sections/_products";