.products-section {

	// .products-section__body

	&__body {
		display: flex;
		flex-direction: column;
	}

	// .products-section__top

	&__top {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	// .products-section__title

	&__title {
		@include adaptiv-value("font-size", 28, 22, 1);
		font-weight: 600;
		line-height: 34px;
		color: $colorDark;
	}

	// .products-section__items

	&__items {
		gap: 20px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(5, 1fr);
	}
}